.burger{
    display: none;
    @include high-tablet{
        display: block;
        position: relative;
        width: 44px;
        height: 44px;
        border: 1px solid rgba(255, 255, 255, 0.08);
        border-radius: 10px;
    &__line {
        position: absolute;
        left: 50%;
        top: 50%;
        border-radius: 100px;
        width: 26px;
        height: 2px;
        background-color: var(--light-color);
        transform: translateX(-50%) translateY(-50%);
    
        &:first-child {
          margin-top: -7px;
        }
        &:last-child {
            left: 9px;
            margin-top: 7px;
            width: 13px;
            transform: translateY(-50%);
          }
    }
    }
}
.burger-close{
    display: none;
    @include high-tablet{
        display: block;
        position: absolute;
        right: var(--container-offset);
        top: 30px;
        width: 44px;
        height: 44px;
        border: 1px solid rgba(255, 255, 255, 0.08);
        border-radius: 10px;
        background-image: url('../img/close-burger.svg');
        background-position: center;
        background-size: 24px 24px;
        background-repeat: no-repeat;
    }
}