.btn{
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--accent-color);
    border-radius: 5px;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    color: #FFFFFF;
    padding: 25px 30px;
    min-width: 260px;

    &--dark{
        background-color: var(--dark-color);
    }
}