.form{
    display: flex;
    flex-direction: column;
    width: 100%;
    &__label{
        padding-bottom: 10px;
        width: 100%;
        display: flex;
        flex-direction: column-reverse;
    }
    .just-validate-error-label{
        color: var(--light-color) !important;
        margin-bottom: 3px;
    }
    &__input{
        width: 100%;
        height: 64px;
        border-radius: 5px;
        padding: 0 20px;
        font-weight: 400;
        font-size: 18px;
        line-height: 26px;
        color: var(--dark-color);
        &::placeholder{
            color: var(--text-color);
        }
    }
    &__btn{
        padding: 18px 30px;
    }
}