.trainers{
    &__title{
        font-weight: 600;
        font-size: 50px;
        line-height: 160%;
        color: var(--dark-color);
    }
    &__wrapper{
        padding-left: max(15px, calc((100% - var(--content-width)) / 2));
    }
    .swiper-slide{
        width: 360px;

        @include mobile{
           width: 220px;
        }
    }
    &__content{
        margin-bottom: 75px;
    }
    &__image{
        max-height: 500px;
        border-radius: 20px;
    }
    &__name{
        font-weight: 600;
        font-size: 24px;
        line-height: 160%;
        color: var(--dark-color);
    }
    &__post{
        font-weight: 400;
        font-size: 18px;
        line-height: 160%;
        color: var(--text-color);
    }
    &__btn{
        font-weight: 400;
        font-size: 18px;
        line-height: 160%;
        color: var( --accent-color);
    }
    &__nav{
        @include flex-all-sb;
    }
    &__scroll{
        background: rgba(20, 16, 36, 0.08);
        border-radius: 40px;
        width: 360px;
        height: 5px;
    }
    .swiper-scrollbar-drag{
        background-color: var(--dark-color)
    }
    &__btns{
        @include flex-v-center;

        @include mobile{
           display: none;
         }
    }
    &__slider-btn {
        @include bg-cover;
    
        width: 40px;
        height: 40px;
    
        &--prev {
          background-image: url("../img/prev.svg");
        }
    
        &--next {
          background-image: url("../img/next.svg");
        }
    
        &:not(:last-child) {
          margin-right: 10px;
        }
      }
}